.conteiner {
  .path {
    height: 600px;
    width: 100%;
    background-image: url("../../assets/Blog/c2672e91aab7b8fcf98747a80bb19520f05a93ff.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .blog {
    margin: 0 148px;
    border-width: 100%;

    .conteinerWrapper {
      margin: 100px 0 0 0;

      h1 {
        font-family: "Italiana", sans-serif;
        font-weight: 400;
        font-size: 32px;
        color: var(--color-text);
      }
      a {
        text-decoration: none;
      }
      .newPost {
        margin: 32px 0 0 0;
        width: 100%;
        height: 540px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 0 0 16px 16px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        .BlogContent {
          display: flex;
          justify-content: center;
          flex-direction: column;
          padding: 16px;
          height: 149px;
          backdrop-filter: blur(30px);
          background: rgba(88, 88, 88, 0.4);
          border-radius: 16px;
          h1 {
            font-size: 24px;
            font-weight: 500;
            color: rgba(255, 255, 255, 1);
            font-family: "Manrope", sans-serif;
          }
          h2 {
            font-family: "Manrope", sans-serif;
            font-weight: 400;
            font-size: 16px;
            color: rgba(220, 220, 220, 1);
          }
          .date {
            margin: 22px 0;
            display: flex;
            justify-content: space-between;
            p {
              font-size: 12px;
              font-family: "Manrope", sans-serif;
              font-weight: 400;
              color: rgba(255, 255, 255, 1);
            }
          }
        }
      }
    }
  }
  .recentPost {
    width: 100%;
    height: 100%;
    margin: 72px 0;
    .postsConteiner {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;

      .posts {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin: 45px 0;
        cursor: pointer;
        width: auto;
        height: 430px;
        a {
          text-decoration: none;

          img {
            width: 100%;
            height: 250px;
            border-radius: 12px;
          }
          p {
            font-family: "Manrope", sans-serif;
            color: var(--color-text);
            font-size: 14px;
          }
          h1 {
            color: var(--color-text);
            height: 60px;
            font-size: 24px;
            font-family: "Manrope", sans-serif;
          }
          p:nth-of-type(2) {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;

            color: var(--color-text);
            font-family: "Manrope", sans-serif;

            font-size: 14px;
          }

          .date {
            display: flex;
            justify-content: space-between;
            p {
              font-size: 12px;
            }
            p:nth-of-type(1) {
              color: var(--color-text);
              font-size: 12px;
            }
          }
        }
      }
    }
  }
  .popularPosts {
    margin: 64px 148px;
    h1 {
      font-family: "Italiana", sans-serif;
      font-weight: 400;
      font-size: 32px;
      color: var(--color-text);
    }
  }
}

@media screen and (max-width: 1350px) {
  .conteiner {
    .blog {
      margin: 0 120px;
     
    }
    .recentPost {
      .postsConteiner {
        grid-template-columns: repeat(2, 1fr);
      }
    }
  }
  
}
@media screen and (max-width: 1000px) {
  .conteiner {
    .path {
      display: none;
    }
    .blog {
      margin: 0 28px;
      .conteinerWrapper {
        margin: 50px 0 0 0;
        .newPost{
.BlogContent{
  h1{
    font-size: 20px;
  }
}
        }
      }
    }
    .recentPost {
      .postsConteiner {
        grid-template-columns: repeat(2, 1fr);
        .posts {
          margin: 30px 0 0 0;
          a{
  h1{
            font-size: 20px;
          }
          }
        
        }
      }
    }
  }
  
}
@media screen and (max-width: 750px) {
  .conteiner {
    .recentPost {
      .postsConteiner {
        grid-template-columns: repeat(1, 1fr);
        .posts {
          margin: 30px 0 0 0;
          a{
            img{
              height: 300px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .conteiner {
    .blog {
      .conteinerWrapper{
  .newPost {
        border-radius: 20px;
        height: 500px;
      }
      }
    
    }
    .recentPost {
      .postsConteiner {
        grid-template-columns: repeat(1, 1fr);
        .posts {
          margin: 30px 0 0 0;
        }
      }
    }
  }
}
