.expertsConteiner {
  height: 824px;
  width: 100%;
  .ExpertsTitle {
    display: flex;
    padding: 120px 0 56px 140px;
    h1 {
      &:nth-child(1) {
        width: 276px;
        font-family: "Italiana", sans-serif;
        font-weight: 400;
        font-style: normal;
        font-size: 40px;
      }
      &:nth-child(2) {
        font-family: "MyCustomFont", sans-serif;
        font-weight: 400;
        font-size: 60px;
        color: rgba(230, 100, 69, 1);
      }
    }
  }
  .experts {
    width: 100%;
    height: 416px;
    margin: 56px 0 0 0;
    display: flex;

    gap: 14px;
    .expertsProfil {
      display: flex;
      align-items: flex-end;
      justify-content: center;
      padding: 20px;
      width: auto;
      height: auto;
      border-radius: 12px;
      background-repeat: no-repeat;
      background-size: cover;
      .View {
        color: black;
        text-decoration: none;
        p {
          cursor: pointer;
        }
      }
    }
  }
}

[data-theme="dark"] .expertsProfil {
  filter: brightness(0.8) contrast(1.1);
}

@media screen and (max-width: 1350px) {
  .expertsConteiner {
    .ExpertsTitle {
      padding: 120px 0 56px 120px;
    }
  }
}
@media screen and (max-width: 1000px) {
  .expertsConteiner {
    .ExpertsTitle {
      padding: 120px 0 56px 28px;
    }
  }
}
@media screen and (max-width: 600px) {
  .expertsConteiner {
    .ExpertsTitle {
      h1 {
        font-size: 30px;
        &:nth-child(1) {
        width: 200px;
          font-size: 30px;
        }
        &:nth-child(2) {
          font-size: 50px;
        }
      }
      padding: 120px 0 16px 28px;
    }
  }
}
