.formControl {

    border: none;
}
.select {
    height: 100%;
    border: none;
    &::after {
        content: "";
        position: absolute;
        right: 0;
        height: 24px;
        width: 1px;
        background-color: #ddd;
    }
    
    .iconimg {
        margin-right: 14px;
    }
    span{
        display: flex;
        align-items: center;
     object-fit: cover;
    
    }
}



