.servicesContainer {
  width: 100%;
  height: 100vh;
  background-color: var(--color-bg-secondary);
  background-image: url("../../../assets/HomePage/Vector(2).svg");
  background-position: left;
  background-repeat: no-repeat;

  [data-theme="dark"] .img {
    filter: brightness(0.8) contrast(1.1);
  }
  .servicesWrapper {
    padding: 45px 140px;

    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    justify-content: center;
    h1 {
      font-family: "Italiana", sans-serif;
      font-weight: 400;
      font-style: normal;
      font-size: 40px;
      span {
        font-family: "MyCustomFont", sans-serif;
        font-weight: 400;
        line-height: 35px;
        font-size: 40px;
        color: red;
      }
    }
    .button {
      width: 100%;
      display: flex;
      justify-content: center;
      button {
        font-size: 18px;
        cursor: pointer;
        border: none;
        border-radius: 4px;
        background-color: inherit;
        border: 1px solid rgba(137, 110, 88, 1);
        height: 46px;
        width: 200px;
        color: #896e58;
        &:hover {
          box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
        }
      }
    }

    .servicesImages {
      display: flex;
      justify-content: space-around;
      gap: 20px;
      margin: 50px 0;

      .imageWrapper {
        position: relative;
        width: 100%;
        height: 300px;
        border-radius: 12px;
        overflow: hidden;

        .img {
          object-fit: cover;
          width: 100%;
          height: 100%;
          border-radius: 12px;
          transition: 0.3s ease-in-out;
        }
        .hoverimg {
          transition: 0.3s ease-in-out;
          background-color: linear-gradient(0deg, #d9d9d9, #d9d9d9),
            linear-gradient(0deg, rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35));
          object-fit: cover;
          width: 100%;
          height: 100%;
          border-radius: 12px;
          transition: 0.3s ease-in-out;
        }
        .hoverButton {
          cursor: pointer;
          width: 100%;
          height: 57px;
          position: absolute;
          bottom: 0;
          left: 50%;
          border-radius: 0 12px;
          transform: translateX(-50%);
          padding: 10px 20px;
          background-color: rgba(230, 100, 69, 1);
          color: white;
          border: none;

          opacity: 0.9;
          transition: 0.3s;
        }
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .servicesContainer {

    .servicesWrapper {
      padding: 45px 28px;
    }
  }
}
@media screen and (max-width: 600px) {
  .servicesContainer {

    .servicesWrapper {
      padding: 45px 28px;
      h1{
        font-size: 30px;
         span{
        font-size: 30px;
      }
      }
     
    }
  }
}
