.modal {
  display: flex;
  justify-content: center;
  align-items: center;

  backdrop-filter: blur(4px);
}
.parentModal {
  display: flex;
  justify-content: center;
  .SeeMore {
    border: none;
    background-color: rgba(212, 163, 115, 1);
    width: 199px;
    height: 48px;
    border-radius: 4px;
    color: white;
    cursor: pointer;
  }
}
.modalWrapper {
  position: relative;
  max-width: 700px;
  width: 90vw;
  border-radius: 16px;

  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modalImageCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 6px;
  width: 100%;
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.modalImage {
  border-radius: 8px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.5);
  object-fit: cover;
  display: block;
}

.arrowLeft,
.arrowRight {
  display: flex;

  font-size: 32px;
  background: rgba($color: #000000, $alpha: 0.2);
  border: none;
  cursor: pointer;
  padding: 10px;
  border-radius: 50%;
  z-index: 2;
  color: white;
  transition: background 0.3s ease;
}

.arrowLeft:hover,
.arrowRight:hover {
  background: rgba(255, 255, 255, 0.7);
}

.expertsprofile {
  width: 100%;

  .profileContent {
    margin: 60px 0 0 0;
    padding: 0 140px;
    .profile {
      background-image: url("../../../../assets/Experts/Text\ input.png");
      background-position: right;
      background-repeat: no-repeat;
      display: flex;
      gap: 20px;
      height: 450px;
      border-radius: 12px;
      .img {
        border-radius: 12px;
        height: 100%;
        width: 500px;
        img {
          background-size: contain;
          object-fit: cover;
          width: 100%;
          border-radius: 12px;
          height: 100%;
        }
      }

      .text {
        margin: 20px 0;
        width: 58%;
        height: 100%;
        .experienceWrapper {
          display: flex;
          align-items: center;
          justify-content: space-between;
          max-width: 800px;
          margin: 0 auto;
          padding: 20px 0;
          font-family: sans-serif;

          .year {
            font-size: 14px;
            color: #777;
          }

          .lineWrapper {
            display: flex;
            align-items: center;
            flex: 1;
            margin: 0 10px;
            position: relative;

            .line {
              height: 1px;
              background-color: #f77a2f;
              flex: 1;
            }

            .title {
              font-family: "Manrope", sans-serif;
              padding: 0 15px;
              font-size: 16px;
              font-weight: 600;
              color: var(--color-text);

              position: relative;
              z-index: 1;
              white-space: nowrap;
            }

            .dot {
              width: 6px;
              height: 6px;
              background-color: #f77a2f;
              border-radius: 50%;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
            }

            .leftDot {
              left: 0;
            }

            .rightDot {
              right: 0;
            }
          }
        }

        h1 {
          font-weight: 300;
          font-size: 32px;
          font-family: "Manrope", sans-serif;
          color: var(--color-text);
        }
        p {
          margin: 48px 0;
          font-family: "Manrope", sans-serif;
          font-size: 18px;
          line-height: 28px;
          color: var(--color-text-secondary);
        }
      }
    }
    .worksConteiner {
      margin: 144px 0 90px 0;
      h1 {
        font-family: "Italiana", sans-serif;
        font-weight: 400;
        font-size: 36px;
      }
      .myworks {
        margin: 36px 0 0 0;
        width: 100%;
        // height: 768px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        .worksWrapper {
          cursor: pointer;
          img {
            border-radius: 6px;
            width: 100%;
            height: 275px;
            object-fit: cover;
          }
        }
      }
    }
    .Certificates {
      .Dotwo {
        margin: 80px 0;
        width: 100%;
        height: 1px;
        background-color: rgba(217, 217, 217, 1);
      }
      .CertificatesWrapper {
        h1 {
          font-family: "Italiana", sans-serif;
          font-weight: 400;
          font-size: 36px;
        }
        .imgCont {
          display: grid;
          grid-template-columns: repeat(4, 1fr);

          .img {
            width: 100%;
            margin: 32px 0;

            img {
              border-radius: 12px;
              width: 270px;
              height: 200px;

              gap: 10px;
              object-fit: cover;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .expertsprofile {
    .profileContent {
      padding: 0 120px;
      .profile {
        .text {
          margin: 0;
          p {
            margin: 30px 0;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 950px) {
  .expertsprofile {
    .profileContent {
      padding: 0 28px;
      .profile {
        height: auto;
        flex-direction: column;
        align-items: center;
        .text {
          width: 100%;
          margin: 20px 0;
        }
        img {
          width: 400px;
        }
      }
      .worksConteiner {
        margin: 100px 0 90px 0;
        .myworks {
          grid-template-columns: repeat(3, 1fr);
        }
      }
    }
  }
}
@media screen and (max-width: 600px) {
  .expertsprofile {
    .profileContent {
      .profile{
        .img{
          width: 100%;
        }
      }
      .worksConteiner {
        .myworks {
          grid-template-columns: repeat(2, 1fr);
        }
      
      }
        .Certificates {
          .CertificatesWrapper {
            .imgCont {
              display: grid;
              grid-template-columns: repeat(2, 1fr);
              gap: 20px;
              width: 100%;
             .img{
              img{

   width: 100%;
              }
             
              }
            }
          }
        }
    }
  }
}
