.bookingCont {
  width: 100%;
  height: 79px;
  position: relative;
  display: flex;
  justify-content: center;
  background-color: rgb(251, 247, 242);
  .boogkingWrapper {
    padding: 16px;
    border: 1px solid rgb(247, 247, 247);
    position: absolute;
    bottom: 30px;
    z-index: 15;
    width: 80%;
    height: 109px;
    border-radius: 16px;
    background-color: white;
    box-shadow: 0px 0px 1px 0px rgba(201, 201, 201, 0.1),
      0px 2px 2px 0px rgba(201, 201, 201, 0.09),
      0px 4px 3px 0px rgba(201, 201, 201, 0.05),
      0px 8px 3px 0px rgba(201, 201, 201, 0.01),
      0px 12px 3px 0px rgba(201, 201, 201, 0);

    .inputPicker {
      position: relative;
      width: 100%;
      height: 100%;
      border: 2px solid rgba(212, 212, 212, 0.19);
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 16px;
      .inputWrapper {
        width: 23%;
      }
      .time {
        width: 23%;
        height: 100%;
      }
      .bookbutton {
        background: rgba(212, 163, 115, 1);
        color: white;
        border-radius: 4%;
        height: 40px;
        width: 104px;
        border: none;
        cursor: pointer;
        font-size: 16px;
        font-weight: 500;
      }
    }
  }
}
@media screen and (max-width: 1300px) {
  .bookingCont {
    .boogkingWrapper {
      width: 90%;
    }
  }
}
@media screen and (max-width: 1100px) {
  .bookingCont {
    .boogkingWrapper {
      width: 95%;
    }
  }
}
@media screen and (max-width: 1000px) {
  .bookingCont {
    .boogkingWrapper {
      height: auto;
      width: 95%;

      .inputPicker {
        border-radius: 16px;
        padding: 0px;
        display: grid;
        grid-template-columns: 1fr 1fr; 
        grid-template-rows: auto auto auto;
 

        .inputWrapper,
        .time,
        .btnconteiner,
        > div { 
          width: 100%;   
          min-width: 0;  
        }

        .inputWrapper {
          border: 1px solid rgba(220, 220, 220, 1);
          border-radius: 16px 0 0 0;
        }

        .time {
          border: 1px solid rgba(220, 220, 220, 1);
          border-radius: 0 0 16px 0;
        }

        .btnconteiner {
          grid-column: 1 / -1; 
          display: flex;
          justify-content: center;
          margin: 10px 0;

          button {
            width: 90%;
            height: 60px;
            border-radius: 8px;
          }
        }
      }
    }
  }
}
