.confirmContainer {
  max-width: 500px;
  margin: 60px auto;
  padding: 20px;
  font-family: "Arial", sans-serif;
  color: #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h2 {
    font-family: "Manrope", sans-serif;
    font-size: 32px;
    font-weight: 500;
    text-align: center;
    margin: 0 0 24px 0;
  }

  p {
    text-align: center;
    font-family: "Manrope", sans-serif;
    font-size: 16px;
    color: rgba(101, 101, 101, 1);
    font-weight: 400;
  }

  .codeinputs {
    padding: 0;
    display: flex;
    justify-content: center;
    gap: 50px;
    margin: 40px 0 13px 0;

    input {
      width: 48px;
      height: 48px;
      font-size: 28px;
      text-align: center;
      border: 1px solid #0f0f0f;
      border-radius: 8px;
      outline: none;
      transition: border-color 0.2s;

      &:focus {
        border-color: #333;
      }
    }
  }

  .resendtext {
    font-size: 12px;
    color: rgba(124, 124, 124, 1);
    font-family: "Manrope", sans-serif;
    margin: 0 0 45px 0;
    .resendlink {
      color: #0a75c2;
      cursor: pointer;
    }
  }

  .confirmbtn {
    background-color: #ccc;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    color: white;
    font-size: 16px;
    cursor: not-allowed;
    transition: background-color 0.3s;
    width: 100%;

    &:not(:disabled) {
      background-color: rgba(212, 163, 115, 1);
      cursor: pointer;
    }
  }
}
@media screen and (max-width: 1000px) {
  .confirmContainer {
    margin: 0;
    width: 100%;
    h2 {
      color: white;
    }
    p {
      color: rgba(239, 239, 239, 1);
    }
    .resendtext {
  margin: 0 0 25px 0;
        color: rgba(239, 239, 239, 1);
      
    }

    .codeinputs {
      gap: 40px;
    }
  }
}
@media screen and (max-width: 800px) {
  .confirmContainer {
    margin: 0;
    padding: 0;
    width: 100%;
    .codeinputs {
      gap: 25px;
    }
  }
}

@media screen and (max-width: 500px) {
  .confirmContainer {
    margin: 0;
    padding: 0;
    width: 100%;
    .codeinputs {
      gap: 15px;
      input{
  background: transparent;
      border: 1px solid white;
      width: 45px;
      }
    
    }
  }
}

